import Container from "@/shared/ui/Container";
import Modal from "@/shared/ui/Modal";
import { founders } from "@/content/site";
import { useActiveSectionId } from "@/shared/lib/activeSectionContext";
import { useLocale } from "@/shared/lib/localeContext";
import { useEffect, useMemo, useState } from "react";
import { motion } from "framer-motion";

type Founder = {
  id: number;
  name: string;
  role: string;
  photo: string;
  bio: string;
};

function FounderCard({
  person,
  onOpen,
  index = 0,
}: {
  person: Founder;
  onOpen: (p: Founder) => void;
  index?: number;
}) {
  return (
    <motion.button
      type="button"
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, delay: index * 0.1, ease: [0.22, 1, 0.36, 1] }}
      viewport={{ once: true, margin: "-50px" }}
      onClick={() => onOpen(person)}
      className="founder-card founder-hero"
      aria-label={`  ${person.name}`}
    >
      <button
        type="button"
        tabIndex={-1}
        className="founder-photo founder-gallery-photo"
        aria-hidden="true"
      >
        <img src={person.photo} alt={person.name} className="h-full w-full object-contain" loading="lazy" />
      </button>
      <div className="founder-caption">
        <div>{person.name}</div>
        <div>{person.role}</div>
      </div>
    </motion.button>
  );
}

export default function Founders() {
  const { locale } = useLocale();
  const activeId = useActiveSectionId();
  const [active, setActive] = useState<Founder | null>(null);
  const t = founders[locale];
  const people = useMemo<Founder[]>(() => t.people as Founder[], [t.people]);

  return (
    <section id="founders" className="py-10 sm:py-12 md:py-14">
      <Container>
        <div>
          <div>
            <div
              className={
                activeId === "founders"
                  ? "text-[11px] sm:text-[12px] uppercase tracking-luxe text-red-400 underline decoration-red-400/80 underline-offset-4"
                  : "text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash"
              }
              data-active-anchor
            >
              {t.title}
            </div>
            <h2 className="mt-3 sm:mt-4 text-[28px] sm:text-2xl md:text-3xl font-semibold tracking-[-0.01em] leading-tight">
              {t.subtitle}
            </h2>
          </div>

          <div className="mt-6 sm:mt-8">
            <div className="grid gap-4 sm:gap-5 grid-cols-1 sm:grid-cols-3 lg:grid-cols-5">
              {people.map((p, idx) => (
                <FounderCard key={p.id} person={p} onOpen={setActive} index={idx} />
              ))}
            </div>
                    </div>
                  </div>

        <Modal
          open={!!active}
          onClose={() => setActive(null)}
          title={active?.name}
          surfaceClassName="founder-modal rounded-2xl"
        >
          {active && (
            <div className="max-h-[calc(100dvh-2rem)] overflow-y-auto overscroll-contain touch-pan-y hide-scrollbar" style={{ position: 'relative' }}>
              {/* Close button - absolute positioned */}
              <button
                className="modal-close-btn"
                onClick={() => setActive(null)}
                type="button"
                aria-label=""
                style={{ position: 'absolute', right: '12px', top: '12px', zIndex: 100 }}
              >
                
              </button>
              
              <div className="founder-modal-body" style={{ backgroundImage: `url(${active.photo})` }}>
                <div className="founder-modal-content">
                  <p className="text-[14px] sm:text-sm md:text-[15px] leading-relaxed text-paper/90 whitespace-pre-line">
                    {active.bio}
                  </p>
                </div>
              </div>
            </div>
          )}
        </Modal>
      </Container>
    </section>
  );
}
