import Container from "@/shared/ui/Container";
import Modal from "@/shared/ui/Modal";
import { events, infocus } from "@/content/site";
import { useActiveSectionId } from "@/shared/lib/activeSectionContext";
import { useLocale } from "@/shared/lib/localeContext";
import { useEffect, useMemo, useRef, useState } from "react";
import { motion } from "framer-motion";

type EventItem = {
  id: number;
  title: string;
  date: string;
  folder: string;
  cover: string;
  photos: number;
  description: string;
  details: string;
};

type EventsPayload = {
  uk: { title: string; subtitle: string; note: string; items: EventItem[] };
  en: { title: string; subtitle: string; note: string; items: EventItem[] };
};

function buildImages(folder: string, total: number) {
  return Array.from({ length: total }, (_, i) => `/events/${folder}/${i + 1}.webp`);
}

function Card({ e, onOpen, index }: { e: EventItem; onOpen: (event: EventItem) => void; index: number }) {
  return (
    <motion.button
      type="button"
      onClick={() => onOpen(e)}
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.6, delay: index * 0.1, ease: [0.22, 1, 0.36, 1] }}
      viewport={{ once: true, margin: "-50px" }}
      className="group rounded-xl2 border border-hairline bg-panel p-5 sm:p-6 md:p-7 card-gold transition-colors text-left"
    >
      <div className="h-32 sm:h-36 rounded-xl2 border border-hairline bg-white/5 overflow-hidden">
        <img src={e.cover} alt={e.title} className="h-full w-full object-cover" loading="lazy" />
      </div>
      <div className="mt-4 sm:mt-5 flex flex-col gap-3">
        <div>
          <div className="text-[15px] sm:text-sm font-semibold leading-tight">{e.title}</div>
          <div className="mt-2 text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash">{e.date}</div>
        </div>
        <p className="text-[14px] sm:text-sm text-ash leading-relaxed">{e.description}</p>
        <div className="text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash">
          : {e.photos}
        </div>
      </div>
    </motion.button>
  );
}

export default function InFocus() {
  const { locale } = useLocale();
  const [active, setActive] = useState<EventItem | null>(null);
  const [activeImage, setActiveImage] = useState<string | null>(null);
  const [activeIndex, setActiveIndex] = useState<number>(0);
  const [showArchive, setShowArchive] = useState(false);
  const [eventsData, setEventsData] = useState<EventsPayload | null>(null);
  const activeId = useActiveSectionId();
  const touchStartX = useRef<number | null>(null);
  const touchStartY = useRef<number | null>(null);
  useEffect(() => {
    let isMounted = true;
    fetch("/events/events.json")
      .then((res) => (res.ok ? res.json() : null))
      .then((data) => {
        if (!isMounted || !data) return;
        console.log("Events JSON loaded:", data);
        setEventsData(data as EventsPayload);
      })
      .catch((err) => {
        console.error("Error loading events JSON:", err);
      });
    return () => {
      isMounted = false;
    };
  }, []);
  const inFocusT = infocus[locale];
  const t = (eventsData?.[locale] ?? events[locale]) as EventsPayload["uk"];
  const items = useMemo(() => t.items as EventItem[], [t.items]);
  const recentItems = items.slice(0, 3);
  const archiveItems = items.slice(3);
  const galleryImages = active ? buildImages(active.folder, active.photos) : [];
  const hasGallery = galleryImages.length > 0;
  const goPrev = () => {
    if (!hasGallery) return;
    const nextIndex = (activeIndex - 1 + galleryImages.length) % galleryImages.length;
    setActiveIndex(nextIndex);
    setActiveImage(galleryImages[nextIndex]);
  };
  const goNext = () => {
    if (!hasGallery) return;
    const nextIndex = (activeIndex + 1) % galleryImages.length;
    setActiveIndex(nextIndex);
    setActiveImage(galleryImages[nextIndex]);
  };
  return (
    <section id="events" className="py-14">
      <div id="focus" aria-hidden="true" className="sr-only" />
      <Container>
        <div className="flex flex-col sm:flex-row sm:items-end sm:justify-between gap-4 sm:gap-6">
          <div className="flex-1">
            <div
              className={
                activeId === "events"
                  ? "text-[11px] sm:text-[12px] uppercase tracking-luxe text-red-400 underline decoration-red-400/80 underline-offset-4"
                  : "text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash"
              }
              data-active-anchor
            >
              {inFocusT.title}
            </div>
            <h2 className="mt-3 sm:mt-4 text-[28px] sm:text-2xl md:text-3xl font-semibold tracking-[-0.01em] leading-tight">
              {inFocusT.subtitle}
            </h2>
            <p className="mt-4 text-[14px] sm:text-base text-ash leading-relaxed max-w-[72ch]">
              {inFocusT.note}
            </p>
          </div>
          {archiveItems.length > 0 && (
            <button
              type="button"
              onClick={() => setShowArchive((prev) => !prev)}
              className="hidden md:inline-flex text-[12px] uppercase tracking-luxe text-ash hover:text-paper transition-colors"
            >
              {showArchive ? inFocusT.hideArchiveLabel : inFocusT.archiveLabel} 
            </button>
          )}
        </div>

        <div className="mt-6 sm:mt-8 grid gap-3 sm:gap-4 md:grid-cols-3">
          {recentItems.map((e, idx) => (
            <Card key={e.id} e={e} onOpen={setActive} index={idx} />
          ))}
        </div>

        {showArchive && archiveItems.length > 0 && (
          <div className="mt-8">
            <div className="text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash"></div>
            <div className="mt-3 sm:mt-4 grid gap-3 sm:gap-4 md:grid-cols-3">
              {archiveItems.map((e, idx) => (
                <Card key={e.id} e={e} onOpen={setActive} index={idx} />
              ))}
            </div>
          </div>
        )}

        <Modal
          open={!!active}
          onClose={() => {
            setActive(null);
            setActiveImage(null);
            setActiveIndex(0);
          }}
          title={active?.title}
          surfaceClassName="rounded-2xl border border-hairline bg-[#0B0C0E] shadow-2xl"
        >
          {active && (
            <div className="max-h-[calc(100dvh-2rem)] overflow-y-auto overscroll-contain touch-pan-y hide-scrollbar" style={{ position: 'relative' }}>
              {/* Close button - absolute positioned */}
              <button
                className="modal-close-btn"
                onClick={() => {
                  setActive(null);
                  setActiveImage(null);
                  setActiveIndex(0);
                }}
                type="button"
                aria-label=""
                style={{ position: 'absolute', right: '12px', top: '12px', zIndex: 100 }}
              >
                
              </button>
              
              <div className="p-6 md:p-7">
                <div>
                  <div className="text-[12px] uppercase tracking-luxe text-ash">{inFocusT.title}</div>
                  <div className="mt-2 text-xl md:text-2xl font-semibold">{active.title}</div>
                  <div className="mt-1 text-sm text-ash uppercase tracking-luxe">{active.date}</div>
                </div>

                <div className="mt-5">
                  <div className="rounded-xl2 border border-hairline bg-white/5 overflow-hidden">
                    <img src={active.cover} alt={active.title} className="w-full h-auto object-cover" loading="lazy" />
                  </div>
                  <p className="mt-4 text-sm md:text-[15px] leading-relaxed text-paper/90 whitespace-pre-line">
                    {active.details}
                  </p>
                  <div className="mt-6 grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
                    {galleryImages.map((src, idx) => (
                      <button
                        key={src}
                        type="button"
                        onClick={() => {
                          setActiveImage(src);
                          setActiveIndex(idx);
                        }}
                        className="rounded-xl2 border border-hairline bg-white/5 overflow-hidden text-left"
                      >
                        <img src={src} alt={active.title} className="h-full w-full object-cover" loading="lazy" />
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}
        </Modal>

        {activeImage && (
          <div
            className="fixed inset-0 z-[120] flex items-center justify-center bg-black p-4"
            onTouchStart={(e) => {
              const touch = e.touches[0];
              if (!touch) return;
              touchStartX.current = touch.clientX;
              touchStartY.current = touch.clientY;
            }}
            onTouchEnd={(e) => {
              const touch = e.changedTouches[0];
              if (!touch || touchStartX.current === null || touchStartY.current === null) return;
              const dx = touch.clientX - touchStartX.current;
              const dy = touch.clientY - touchStartY.current;
              touchStartX.current = null;
              touchStartY.current = null;
              if (Math.abs(dx) < 40 || Math.abs(dx) < Math.abs(dy)) return;
              if (dx < 0) goNext();
              else goPrev();
            }}
          >
            <button
              type="button"
              className="fullscreen-close-btn"
              onClick={() => setActiveImage(null)}
              aria-label=" "
              style={{ position: 'fixed', right: '16px', top: '16px', left: 'auto', zIndex: 130 }}
            >
               
            </button>
            <button
              type="button"
              className="hidden md:flex absolute left-4 top-1/2 -translate-y-1/2 rounded-full border border-hairline px-3 py-2 text-sm text-ash hover:text-paper transition-colors"
              onClick={goPrev}
              aria-label=" "
            >
              
            </button>
            <img
              src={activeImage}
              alt={active?.title}
              className="max-h-[90vh] w-auto max-w-[92vw] object-contain"
            />
            <button
              type="button"
              className="hidden md:flex absolute right-4 top-1/2 -translate-y-1/2 rounded-full border border-hairline px-3 py-2 text-sm text-ash hover:text-paper transition-colors"
              onClick={goNext}
              aria-label=" "
            >
              
            </button>
          </div>
        )}
      </Container>
    </section>
  );
}
