import Container from "@/shared/ui/Container";
import Modal from "@/shared/ui/Modal";
import { team } from "@/content/site";
import { useActiveSectionId } from "@/shared/lib/activeSectionContext";
import { useLocale } from "@/shared/lib/localeContext";
import { useEffect, useMemo, useState } from "react";

type Person = {
  id: number;
  name: string;
  role: string;
  photo: string;
  bio: string;
};

function PersonCard({
  person,
  onOpen,
}: {
  person: Person;
  onOpen: (p: Person) => void;
}) {
  return (
    <div className="rounded-3xl border border-hairline bg-panel p-5 sm:p-6 md:p-8 card-gold transition-colors text-center">
      <button
        type="button"
        onClick={() => onOpen(person)}
        className="mx-auto h-56 w-56 sm:h-48 sm:w-48 md:h-32 md:w-32 rounded-full border border-hairline bg-white/5 overflow-hidden block"
        aria-label={`  ${person.name}`}
      >
        <img src={person.photo} alt={person.name} className="h-full w-full object-cover" loading="lazy" />
      </button>
      <div className="mt-5 sm:mt-4 text-[16px] sm:text-[15px] md:text-sm font-semibold">{person.name}</div>
      <div className="mt-2 sm:mt-1 text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash">{person.role}</div>
    </div>
  );
}

export default function Team() {
  const { locale } = useLocale();
  const activeId = useActiveSectionId();
  const [active, setActive] = useState<Person | null>(null);
  const [remotePeople, setRemotePeople] = useState<Person[] | null>(null);
  useEffect(() => {
    let cancelled = false;
    fetch("/api/content/team.php")
      .then((res) => (res.ok ? res.json() : null))
      .then((data) => {
        if (cancelled || !data || !Array.isArray(data.items)) return;
        setRemotePeople(data.items as Person[]);
      })
      .catch(() => undefined);
    return () => {
      cancelled = true;
    };
  }, []);
  const t = team[locale];
  const people = useMemo<Person[]>(() => (remotePeople ?? (t.people as Person[])), [remotePeople, t.people]);

  return (
    <section id="team" className="py-10 sm:py-12 md:py-14">
      <Container>
        <div className="flex flex-col sm:flex-row sm:items-end sm:justify-between gap-4 sm:gap-6">
          <div>
            <div
              className={
                activeId === "team"
                  ? "text-[11px] sm:text-[12px] uppercase tracking-luxe text-red-400 underline decoration-red-400/80 underline-offset-4"
                  : "text-[11px] sm:text-[12px] uppercase tracking-luxe text-ash"
              }
              data-active-anchor
            >
              {t.title}
            </div>
            <h2 className="mt-3 sm:mt-4 text-[28px] sm:text-2xl md:text-3xl font-semibold tracking-[-0.01em] leading-tight">
              {t.subtitle}
            </h2>
          </div>
        </div>

        <div className="mt-6 sm:mt-8 grid gap-4 sm:gap-5 grid-cols-1 sm:grid-cols-2 lg:grid-cols-4">
          {people.map((p) => (
            <PersonCard key={p.id} person={p} onOpen={setActive} />
          ))}
        </div>

        <Modal
          open={!!active}
          onClose={() => setActive(null)}
          title={active?.name}
          containerClassName="items-center"
          surfaceClassName="rounded-2xl border border-hairline bg-[#0B0C0E] shadow-2xl"
        >
          {active && (
            <div className="max-h-[calc(100dvh-2rem)] overflow-y-auto overscroll-contain touch-pan-y hide-scrollbar">
              <div className="p-5 sm:p-6 md:p-7">
                <div className="flex items-start justify-between gap-3 sm:gap-4">
                  <div className="flex items-start gap-3 sm:gap-4 min-w-0">
                    <div className="h-16 w-16 sm:h-20 sm:w-20 rounded-full border border-hairline bg-white/5 overflow-hidden flex-shrink-0">
                      <img src={active.photo} alt={active.name} className="h-full w-full object-cover" loading="lazy" />
                    </div>
                    <div className="min-w-0">
                      <div className="text-[10px] sm:text-[12px] uppercase tracking-luxe text-ash">{t.title}</div>
                      <div className="mt-2 text-[16px] sm:text-xl md:text-2xl font-semibold leading-tight">{active.name}</div>
                      <div className="mt-1 text-[12px] sm:text-sm text-ash uppercase tracking-luxe">{active.role}</div>
                    </div>
                  </div>
                  <button className="rounded-full px-2 py-1 text-ash hover:text-paper transition-colors flex-shrink-0" onClick={() => setActive(null)} type="button">
                    
                  </button>
                </div>

                <div className="mt-4 sm:mt-5">
                  <p className="text-[14px] sm:text-sm md:text-[15px] leading-relaxed text-paper/90 whitespace-pre-line">
                    {active.bio}
                  </p>
                </div>
              </div>
            </div>
          )}
        </Modal>
      </Container>
    </section>
  );
}
